@import "../../../../vars.scss";

.models-list {
  height: max-content;
}

@media (min-width: 768px) {
  .models-list {
    display: flex;
    flex-wrap: wrap;
  }
}


@media (min-width: 1024px) {
  .models-list {
    height: calc(100vh - 220px);
    overflow: scroll;
  }
}
